  let addedCount = 0;
  const shuffledLowBeads = [...lowAspectBeads].sort(() => random() - 0.5);
  for (const bead of shuffledLowBeads) {
    if (addedCount >= 2) break;
    recommendedBeads.push(bead);
    usedBeadIds.add(bead.id);
    addedCount++;
  }

