import React, { useState, useEffect } from 'react';
import LoadingScreen from './LoadingScreen';

const AppInitializer = ({ children }) => {
  const [isInitialized, setIsInitialized] = useState(false);
  const [isCordovaReady, setIsCordovaReady] = useState(false);

  //  Cordova 
  const isCordova = () => {
    return window.cordova !== undefined;
  };

  // 
  const handleInitializationComplete = () => {
    setIsInitialized(true);
    
    //  Cordova 
    if (isCordova() && window.navigator && window.navigator.splashscreen) {
      window.navigator.splashscreen.hide();
    }
  };

  useEffect(() => {
    let initializationTimer;
    
    const initializeApp = () => {
      console.log('...');
      
      // 
      const tasks = [
        // 1. 
        () => new Promise(resolve => {
          console.log('...');
          const images = [
            '/74559e4c-3a36-4268-8486-ed99ce2a1abf.png',
            '/wooden-tray.png',
            '/blackpaper.jpeg'
          ];
          
          const imagePromises = images.map(src => {
            return new Promise((resolve, reject) => {
              const img = new Image();
              img.onload = () => {
                console.log(`: ${src}`);
                resolve();
              };
              img.onerror = () => {
                console.warn(`: ${src}`);
                resolve(); // 
              };
              img.src = src;
            });
          });
          
          Promise.all(imagePromises).then(() => {
            console.log('');
            setTimeout(resolve, 300);
          });
        }),
        
        // 2.  Cordova 
        () => new Promise(resolve => {
          if (isCordova()) {
            console.log(' Cordova ...');
            
            // 
            if (window.cordova && window.cordova.platformId) {
              console.log('Cordova ');
              resolve();
            } else {
              //  deviceready 
              const handleDeviceReady = () => {
                console.log('Cordova deviceready ');
                document.removeEventListener('deviceready', handleDeviceReady);
                resolve();
              };
              
              document.addEventListener('deviceready', handleDeviceReady);
              
              // 
              setTimeout(() => {
                console.log('Cordova ');
                document.removeEventListener('deviceready', handleDeviceReady);
                resolve();
              }, 2000);
            }
          } else {
            console.log(' Cordova  Cordova ');
            resolve();
          }
        }),
        
        // 3. 
        () => new Promise(resolve => {
          console.log('...');
          // 
          try {
            //  localStorage 
            localStorage.setItem('test', 'test');
            localStorage.removeItem('test');
            console.log('');
          } catch (e) {
            console.warn(':', e);
          }
          
          setTimeout(resolve, 200);
        })
      ];
      
      // 
      const runTasks = async () => {
        try {
          for (let i = 0; i < tasks.length; i++) {
            console.log(` ${i + 1}/${tasks.length}`);
            await tasks[i]();
          }
          
          console.log('');
          
          // 
          setTimeout(() => {
            console.log('');
            handleInitializationComplete();
          }, 600);
          
        } catch (error) {
          console.error(':', error);
          // 
          setTimeout(() => {
            handleInitializationComplete();
          }, 1000);
        }
      };
      
      runTasks();
    };

    //  DOM 
    initializationTimer = setTimeout(initializeApp, 50);

    return () => {
      if (initializationTimer) {
        clearTimeout(initializationTimer);
      }
    };
  }, []);

  // 
  if (!isInitialized) {
    return <LoadingScreen onLoadingComplete={() => {}} isInitialized={false} />;
  }

  // 
  return children;
};

export default AppInitializer;
